@import "tailwindcss";

:root,
.dark {
  /* Oxide Design System Colors - Desktop Native */
    --background: 0 0% 4%;
    /* Deep Charcoal */
    --foreground: 0 0% 95%;
    /* Soft White */
  
    --card: 0 0% 7%;
    /* Graphite Grey */
    --card-foreground: 0 0% 95%;
  --popover: 0 0% 4%;
  --popover-foreground: 0 0% 95%;
  
    --primary: 168 100% 50%;
    /* Electric Teal */
    --primary-foreground: 0 0% 4%;
  
    --secondary: 0 0% 11%;
    /* Gunmetal */
    --secondary-foreground: 0 0% 95%;
  
    --muted: 0 0% 11%;
    --muted-foreground: 0 0% 65%;
    /* Muted Grey */
  
    --accent: 0 0% 11%;
    --accent-foreground: 0 0% 95%;
  
    --destructive: 0 100% 59%;
    /* Neon Red */
    --destructive-foreground: 0 0% 95%;
  
    --border: 0 0% 15%;
    /* Stealth Grey */
    --input: 0 0% 15%;
    --ring: 168 100% 50%;
    --radius: 0.5rem;
    /* Smaller radius for native feel */
  
    /* Oxide Brand Colors */
    --electric-teal: 168 100% 50%;
    --deep-charcoal: 0 0% 4%;
    --graphite-grey: 0 0% 7%;
    --gunmetal: 0 0% 11%;
    --soft-white: 0 0% 95%;
    --muted-grey: 0 0% 65%;
    --neon-red: 0 100% 59%;
    --bright-lime: 80 100% 50%;
    --stealth-grey: 0 0% 15%;
  }
  
  * {
    border-color: hsl(var(--border));
  }
  
  body {
    color: hsl(var(--foreground));
    background: hsl(var(--background));
    font-feature-settings: "rlig" 1, "calt" 1;
    font-family: var(--font-inter), -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    /* Native app feel */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-user-select: none;
       -moz-user-select: none;
            user-select: none;
    /* Prevent text selection for native feel */
  }
  
  /* Font Family Classes */
  .font-oxanium {
    font-family: var(--font-oxanium), ui-serif, Georgia, Cambria, "Times New Roman", Times, serif;
  }
  
  .font-roboto-condensed {
    font-family: var(--font-roboto-condensed), ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;
  }
  
  .font-jetbrains-mono {
    font-family: var(--font-jetbrains-mono), ui-monospace, SFMono-Regular, "SF Mono", Consolas, "Liberation Mono", Menlo, monospace;
  }
  
  /* Native scrollbars */
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
  
  ::-webkit-scrollbar-track {
    background: hsl(var(--background));
  }
  
  ::-webkit-scrollbar-thumb {
    background: hsl(var(--muted));
    border-radius: 4px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: hsl(var(--accent));
  }
  
  /* Hide scrollbars but keep functionality for specific elements */
  .no-scrollbar::-webkit-scrollbar {
    display: none;
  }
  
  .no-scrollbar {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

